<template>
  <v-container grid-list-xs fluid>
    <v-row column>
      <v-col cols="12">
        <h2>Hello {{user.username}}, </h2>
      </v-col>

      <v-col cols="12" mt-5>
        <v-container pa-0 fluid grid-list-lg>
          <v-row row wrap>
            <v-col cols="12" sm="4" v-for="(heading,index) in headings"  :key="index">
              <v-card>
                <v-card-title>
                  <h2 class="title"> {{heading.title}} </h2>
                </v-card-title>
                <v-card-text>
                  <h1 class="text-xs-center headline"> {{heading.value}} </h1>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn icon :to="'/account/'+heading.to" replace>
                    <v-icon> {{heading.icon}} </v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-col>

      <v-col cols="12" mt-5>
        <v-row >
          <v-col shrink px-3>
            <h2 class="title">Progress</h2>
          </v-col>
          <v-col grow px-5>
            <v-progress-linear min-width="120" :value="(matrix.progress/4) * 100"></v-progress-linear>
            <h4 class="text-xs-left text-sm-right"> {{matrix.progress}} of 4</h4>
          </v-col>
        </v-row>
      </v-col>

      <v-col cols="12" d-flex mt-5>
        <v-container fluid grid-list-md pa-0>
          <v-row>
            <v-col cols="12" sm="6" xs12 sm6 d-flex>
              <v-card>
                <v-card-title class="title">Direct Downlines</v-card-title>
                <v-card-text>
                  <v-progress-circular indeterminate v-if="downlineLoader"></v-progress-circular>
                  <v-list v-else-if="downlineIsLoaded && downlines.length>=1">
                    <v-list-item class="my-3" v-for="(downline,index) in downlines" :key="index">
                      <v-list-item-avatar color="primary"  size="24">
                        <v-icon color="white" size="10">fa-user</v-icon>
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title class="body-1">
                          #{{downline.username}}
                        </v-list-item-title>
                        <v-list-item-subtitle class="caption">
                          <span >Level: {{downline.level}}</span>
                          <v-progress-linear  :value="(downline.progress/4)*100" />
                        </v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                  <h4 v-else-if="downlineIsLoaded && downlines.length<1">You have no downline</h4>
                </v-card-text>
              </v-card>
            </v-col>

            <v-col cols="12" d-flex>
              <v-card>
                <v-card-title class="headline">News</v-card-title>
                <v-card-text>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>

        </v-container>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
import FeedbackForm from "@/components/FeedbackForm"

export default {
  data(){
    return{
      dprofile:{}
    }
  },
  computed:{
    downlines(){return this.$store.getters.downline},
    downlineLoader(){return this.$store.getters.downlineLoader},
    downlineIsLoaded(){return this.$store.getters.downlineIsLoaded},
    user(){ return this.$store.getters.user},
    matrix(){return this.$store.getters.profile.matrix},
    wallet(){return this.$store.getters.profile.wallet},
    headings(){      
      return [
        {title:"Level",value:this.matrix.level,action:false},
        {title:"Direct Referrals",value:this.matrix.downline,
        action:true,to:'matrix',icon:"fa-adn"},
        {title:"Wallet",value:this.wallet.amount,action:true,
        icon:"fa-credit-card",to:'wallet'},
      ]
    }
  },
  methods:{
    navigate(){
      var item= this.single_item
      var id = item.hash
      this.$router.replace({name:"single",params:{"feed":id,data:this.single_item}})
    },
    getFeeds() {
      var self = this
      this.feedIsLoading = true
      this.feedLoadError = ""
      this.$http.get("system/feed").then((response)=>{
        self.feeds = response.data
        self.feedIsLoading = false
      }).catch((error)=>{
        if(error.response){
          if(error.response.status==400){
            self.feedLoadError = "Invalid request"
          }
        }else{
          self.feedLoadError =  "Failed to connect with the server"
        }
        self.feedIsLoading = false
      }).finally(() =>{
        self.feedIsLoading = false
      })
    },
    silentFeedUpdate(){
      var self = this
      this.$http.get("system/feed").then((response)=>{
        self.feeds = response.data
      })
    },
    add_feed(){
      if(this.feedForm.loading==true){return}
      var self = this
      this.feedFormError=false
      this.feedFormErrorMessage=""
      if(this.feedForm.text.length>1 && this.feedForm.text.length <=255){
        this.feedForm.loading = true
        this.$http.post("system/feed/",{text:this.feedForm.text}).then((response)=>{
          if(response.status=="201"){
            self.snackbar.value = true
            self.snackbar.color = "success"
            self.snackbar.text = "Feed added sucessfully"
            self.feedForm.text = ""
            self.silentFeedUpdate()
          }
          self.feedForm.loading = false
        }).catch((error)=>{
          self.snackbar.value=true
          self.snackbar.color = "error"
          self.snackbar.text = "Error in adding feed"
          self.feedForm.loading = false
        })
      }else if(this.feedForm.text.length>255){
        this.feedFormError=true
        this.feedFormErrorMessage="Maximum length exceeded"
      }else{
        this.feedFormError=true
        this.feedFormErrorMessage="Please provide a text"
      }

    },
    single_pop(item){
      this.single_item = item
      this.single_show = true
    },
    generateRandomInteger(min, max) {
      return Math.floor(min + Math.random()*(max + 1 - min))
    }
  },
  components:{FeedbackForm},
  mounted(){
    
  },
  beforeRouteEnter(to,from,next){
    next(vue=>{
      if(!vue.$store.getters.downlineIsLoaded){
        vue.$store.dispatch('downlineUpdate')
      }
    })
  }
}
</script>


<style>
.v-list__tile__avatar{
  min-width:32px !important;
}

.transbg{
  background: rgba(0,0,0,0.0) !important;
}
</style>
