<template>
    <v-card>
        <v-card-title>
            <h2>Tell us how we can make ZALIDU better</h2>
        </v-card-title>
        <v-card-text>
            <v-layout column>
                <v-text-field v-model="feedbackForm.email" placeholder="Email"
                    hint="(Optional) Provide only if you wish for a response from Zalidu" />
                <v-textarea required v-model="feedbackForm.text" placeholder="Your Message" />
                <v-flex>
                    <v-btn color="primary" :loading="feedbackControl.isLoading" @click="send_feedBack">Send</v-btn>
                    <span :color="snackbar.color">{{snackbar.text}}</span>
                </v-flex>
            </v-layout>
        </v-card-text>
        <v-snackbar top v-model="snackbar.value" :color="snackbar.color" :timeout="3000">
            {{snackbar.text}}
        </v-snackbar>
    </v-card>
</template>


<script>
export default {
    data(){
        return{
            feedbackForm:{
                email:"",text:""
            },
            feedbackControl:{
                isLoading:false
            },
            snackbar:{
                value:false,color:"success",text:""
            }
        }
    },
    methods:{
        send_feedBack(){
            var self = this
            if(this.feedbackControl.isLoading){return}
            self.snackbar.text = ""

            if(this.feedbackForm.text.length>1 && this.feedbackForm.text.length<=1024){
                self.feedbackControl.isLoading = true 
                self.axios.post("system/feedback/",self.feedbackForm).then((response)=>{
                    if(response.status == "201"){
                        self.snackbar.value = true
                        self.snackbar.color = "success"
                        self.snackbar.text = "Feedback sent sucessfully"
                        self.feedbackForm= {email:"",text:""}
                    }
                    self.feedbackControl.isLoading = false
                }).catch((error)=>{
                    self.snackbar.value=true
                    self.snackbar.color = "error"
                    self.snackbar.text = "Error in adding feed"
                    self.feedbackControl.isLoading = false
                }).finally(()=>{
                    self.feedbackControl.isLoading = false
                })
            }else if(this.feedbackForm.text.length>1024){

            }else{

            }
        }
    }
}
</script>

